<template>
  <div id="timeline" :style = 'div_css' >
    <t-divider id = 'divider'>控制台</t-divider>
    <t-slider id = 'timer'
        v-model="value"
        :label = 'time_lst[value]'
        :max="maxi"
        :min="0"
       
      />
    
    <t-radio-group variant="primary-filled" id = 'radio' default-value="half">
          <t-radio-button value="half" @click = 'type = `half`'>半年</t-radio-button>
          <t-radio-button value="one" @click = 'type = `one`'>一年</t-radio-button>
    </t-radio-group>
    <t-switch id = 'switch' v-model = 'auto' size="large" :label="['自动播放：开', '自动播放：关']"></t-switch>
    <text id = 'date'>
      日期：{{time_lst[value]}}
    </text>
  </div>
</template>

<script>
export default {
  name: 'timeline',
  props: {
    height:Number,
    width:Number,
    top:Number,
    left:Number,
  },
  data(){
    return {
      value:0,
      type:'half',
      timer:false,
      auto:false,
    }
  },
  computed:{
    maxi(){
      return this.time_lst.length - 1
    },
    flag(){
      return this.$store.state.flag
    },
    time_lst(){
      if(this.type == 'half'){
        return ['2015-06-26', '2015-12-31', '2016-06-24', '2016-12-30', '2017-06-30', '2017-12-31', '2018-06-30', '2018-12-28', '2019-06-30', '2019-12-27', '2020-06-26', '2020-12-31', '2021-06-25', '2021-12-31', '2022-06-24']
      }
      else{
        return ['2015-12-31', '2016-12-30', '2017-12-31', '2018-12-28', '2019-12-27', '2020-12-31', '2021-12-31', '2022-07-01']
      }
    },
    div_css(){
      //div的css属性从这里写
      let div_height = this.height
      let div_width = this.width
      let div_border = '0px solid black'
      let div_position = 'absolute'
      let div_top = this.top //所有边距固定为10
      let div_left = this.left

      return  "position:" + div_position + ";" +
              "height:" + div_height + "px;" +
              "width:" + div_width + "px;" +
              "border:" + div_border + ";" +
              "left:" + div_left + "px;" +
              "top:" + div_top + "px;" 
              


    },
  },
  watch:{
    flag(){
      this.value = 0
      this.type = this.flag
    },
    type(){
      this.$store.commit({
        type:'change_flag',
        flag:this.type,
      })
    },
    value(){
      this.$store.commit({
        type:'change_date',
        date:this.time_lst[this.value]
      })
      if(this.value == this.time_lst.length - 1){
        this.auto = false
      }
    },
    auto(val){
      if(val){
        this.timer = setInterval(()=>{
          if(this.value<this.time_lst.length){
            this.value++
          }
        },2000)
      }
      else{
        clearInterval(this.timer)
        this.timer = false
      }
    },


  }
  
}
</script>

