<template>
  <div id="bar_view" :style = 'div_css' >
    <t-divider id = 'divider'>产品持仓情况{{flag == 'half'?`(每半年调仓)`:`(每年调仓)`}}</t-divider>
    <svg id = 'bar_svg' class = 'axis' :height = 'height' :width = 'width'
    @click = 'log()'>
      <g id = 'bars' v-if = 'prepare'>
        <g v-for = '(d,i) in data'
          :key = 'i'>
          <rect 
          :height = 'd.rate > 0.5?Math.max(0,yS(0.5)) :Math.max(0,yS(d.rate))'
          :width = 'width / 36 - 10'
          :x = 'margin + 10+ i * (width/36-2)'
          :y = 'd.rate>0.5?height - 5 * margin - yS(0.5):height - 5 * margin - yS(d.rate)'
          :fill = 'd.flag == `1`? `red` : `grey`'
          stroke = 'none'
          rx = 3>
          </rect>
        </g>

        <g v-for = '(d,i) in data'
          :key = 'i'>
          <text
          text-anchor = 'middle'
          :x = 'margin + 10+ i * (width/36-2) + 3'
          :y = 'd.rate>0.5?height - 5 * margin - yS(0.5) - 5:height - 5 * margin - yS(d.rate) - 5'
          :stroke = 'd.rate > 0 ? `red` : `green`'
          
          font-size = 9>
            {{d.flag == '1'?d.rate.toFixed(3) :''}}
          </text>

          <g>
            <text v-for = '(j,k) in parse(d.name)'
              :key = 'k'
              font-size = 9.5
              font-family = 'SimSun'
              text-anchor = 'middle'
              :dx = 'margin + 10+ i * (width/36-2) + 4'
              :dy = 'height - 5 * margin + 12 + k * 11'
              :stroke = 'd.flag == `1`? `red` : `grey`'
              stroke-opacity = .7>
              {{j}}
            </text>
          </g>

        </g>
      </g>
      <g id = 'axis'>
        <line id = 'x_axis'
          :x1 = 'margin'
          :x2 = 'width - margin'
          :y1 = 'height - 5 * margin'
          :y2 = 'height - 5 * margin'
          stroke = 'black'>
          
        </line>
        <line id = 'y_axis'
          :x1 = 'margin'
          :x2 = 'margin'
          :y1 = 'margin'
          :y2 = 'height - 5 * margin'
          stroke = 'black'>
        </line>
        <text
          text-anchor = 'end'
          :dx = 'margin - 5'
          :dy = 'margin + 5'
          font-size = 14>
          0.5
        </text>

        <text
          text-anchor = 'end'
          :dx = 'margin - 5'
          :dy = 'margin + (height - 6 * margin) * 5/7  - 5'
          font-size = 14>
          0
        </text>
        <text
          text-anchor = 'end'
          :dx = 'margin - 5'
          :dy = 'height - 5 * margin - 5'
          font-size = 14>
          -0.2
        </text>

      </g>
      
    </svg>
    
  </div>
</template>

<script>
import * as d3 from 'd3-3.5.17'
export default {

  name: 'bar',
  props: {
    height:Number,
    width:Number,
    top:Number,
    left:Number,
  },
  data(){
    return {
      bar1:{
        data:{},
        flag:{},
      },
      bar2:{
        data:{},
        flag:{},
      },
      margin:30,
      prepare:false,
    }
  },
  mounted(){
    d3.csv('bar_half.csv',d=>{
      this.bar1.flag = d
      this.prepare = true
    })

    d3.csv('bar_half2.csv',d=>{
      this.bar1.data = d
      this.prepare = true
    })

    d3.csv('bar_one.csv',d=>{
      this.bar2.flag = d
      this.prepare = true
    })

    d3.csv('bar_one2.csv',d=>{
      this.bar2.data = d
      this.prepare = true
    })
  },
  computed:{
    date(){
      return this.$store.state.date
    },
    flag(){
      return this.$store.state.flag
    },
    yS(){
      return d3.scale.linear().domain([-0.2,0.5]).range([0,this.height - 6 * this.margin])
    },
    data_raw(){
      if(this.flag == 'half'){
        return this.bar1
      }
      else{
        return this.bar2
      }
    },
    data(){
      //let flags = this.data_raw.flag
      //let datum = this.data_raw.data
      let ret = []
      for(let i = 0;i<this.data_raw.data.length;i++){
        let tmp = Object()
        tmp['name'] = this.data_raw.data[i]['name']
        tmp['rate'] = parseFloat(this.data_raw.data[i][this.date])
        tmp['flag'] = this.data_raw.flag[i][this.date]
        ret.push(tmp)
      }
      return ret
    },
    div_css(){
      //div的css属性从这里写
      let div_height = this.height
      let div_width = this.width
      let div_border = '0px solid black'
      let div_position = 'absolute'
      let div_top = this.top //所有边距固定为10
      let div_left = this.left

      return  "position:" + div_position + ";" +
              "height:" + div_height + "px;" +
              "width:" + div_width + "px;" +
              "border:" + div_border + ";" +
              "left:" + div_left + "px;" +
              "top:" + div_top + "px;" 
              


    },
  },
  methods:{
    parse(str){
      let ret = []
      for(let i = 0;i<str.length;i++){
        ret.push(str[i])
      }
      return ret
    },
    label_style(i){
      let position = 'absolute'
      let left = this.margin + 10+ i * (this.width/36-2) -2
      let top = this.height - 5 * this.margin
      let width = 1
      let font_size = 2
      return 'position:' + position + ';' + 
              'left:' + left + 'px;' + 
              "top:" + top + "px;" +
              "width:" + width + "px;" +
              'font-size:' + font_size + 'px;'
    },
    log(){
      console.log(this.bar1,this.bar2)
      console.log(Object.keys(this.data_raw.data[0]))
      console.log(this.date,this.data)
    },
    diff(day1,day2){
      let date1 = new Date(day1)
      let date2 = new Date(day2)
      return Math.ceil(Math.abs(date1 - date2) / 86400000);
    }
  },
  
  
}
</script>

