import { createStore } from 'vuex'

export default createStore({
    state(){
        return{
            time_list:[],
            flag:'half',
            date:'2015-06-26',
        }
    },
    mutations:{
        change_flag(state,payload){
            
            state.flag = payload.flag
            if(payload.flag == 'half'){
                state.date  = '2015-06-26'
            }
            else{
                state.date  = '2015-12-31'
            }
            
        },
        change_date(state,payload){
            
            state.date = payload.date
            
            
        },

    }

})