<template>
  <div id="curve_view" :style = 'div_css' >
    <t-divider id = 'divider'>alpha筛选策略历史回测曲线{{flag == 'half'?`(每半年调仓)`:`(每年调仓)`}}</t-divider>
    <svg id = 'curve_svg' class = 'axis' :height = 'height' :width = 'width'
    @click = 'log'>
      <g id = 'axis'>
        <line id = 'x_axis'
          :x1 = 'margin'
          :x2 = 'width - margin'
          :y1 = 'height - margin'
          :y2 = 'height - margin'
          stroke = 'black'>
          
        </line>
        <line id = 'y_axis'
          :x1 = 'margin'
          :x2 = 'margin'
          :y1 = 'margin'
          :y2 = 'height - margin'
          stroke = 'black'>
        </line>
        <text
          text-anchor = 'end'
          :dx = 'margin - 5'
          :dy = 'margin + 5'
          font-size = 14>
          10
        </text>

        <text
          text-anchor = 'end'
          :dx = 'margin - 5'
          :dy = 'margin + (height - 2 * margin) * 0.9 - 5'
          font-size = 14>
          1
        </text>
        <text
          text-anchor = 'end'
          :dx = 'margin - 5'
          :dy = 'height - margin - 5'
          font-size = 14>
          0
        </text>

        <text v-if = 'prepare'
          text-anchor = 'start'
          :dx = 'margin '
          :dy = 'height - margin + 15'
          font-size = 14>
          {{data[0]['time']}}
        </text>

        <text v-if = 'prepare'
          text-anchor = 'end'
          :dx = 'width - margin '
          :dy = 'height - margin + 15'
          font-size = 14>
          {{data[data.length - 1]['time']}}
        </text>
      </g>
      <g id = 'curve' v-if = 'prepare'>
        <path 
          :d = 'line(data)'
          fill = 'none'
          stroke = 'red'
          stroke-width = 3>
        </path>

        <path 
          :d = 'line2(data)'
          fill = 'none'
          stroke = 'grey'
          stroke-width = 3>
        </path>

        <rect
        fill = 'white'
        :x = 'xS(diff(data[instant_index].time,data[0].time)) + 2'
        :y = 'margin'
        :height = 'height - 2 * margin - 2 '
        :width = 'width'>
        </rect>

        <text
        :dx = 'xS(diff(data[instant_index].time,data[0].time)) + 2 + 3'
        :dy = 'yS(data[instant_index].rate) - 3'
        stroke = 'red'
        font-size = 12
        >
        {{data[instant_index].rate.toFixed(3)}}
        </text>

        <text
        :dx = 'xS(diff(data[instant_index].time,data[0].time)) + 2 + 3'
        :dy = 'yS(data[instant_index].rate2) + 10'
        stroke = 'grey'
        font-size = 12
        >
        {{data[instant_index].rate2.toFixed(3)}}
        </text>
      </g>

      <g id = 'legend'>
         <text id = 'date'
         text-anchor = 'end'
         :dx = 'width - margin'
         dy = 50
         font-size = 14
         >
          日期：{{this.date}}
        </text>
        <rect 
        :x = 'margin + 20'
        :y = 'margin + 20'
        fill = 'red'
        height = 10
        width = 10
        rx = 3>
        </rect>
        <text
        :dx = 'margin + 40'
        :dy = 'margin + 30'
        fill = 'black'
        height = 10
        width = 10
        font-size = 13
        font-weight = 'bold'>
        alpha排名前10%等权
        </text>

        <rect 
        :x = 'margin + 20'
        :y = 'margin + 40'
        fill = 'grey'
        height = 10
        width = 10
        rx = 3>
        </rect>
        <text
        :dx = 'margin + 40'
        :dy = 'margin + 50'
        fill = 'black'
        height = 10
        width = 10
        font-size = 13
        font-weight = 'bold'>
        36只产品等权
        </text>
      </g>
    </svg>
   
      
    
  </div>
</template>

<script>
import * as d3 from 'd3-3.5.17'
export default {
  name: 'curve',
  props: {
    height:Number,
    width:Number,
    top:Number,
    left:Number,
  },
  data(){
    return {
      margin:30,
      data1:[],
      data2:[],
      prepare:false,
    }
  },
  mounted(){
    
    d3.csv('line_half.csv',d=>{
      this.data1 = d
      this.prepare = true
    })

    d3.csv('line_one.csv',d=>{
      this.data2 = d
      this.prepare = true
    })
    
  },
  computed:{
    instant_index(){
      let ret = 0
      for(let i = 0 ;i<this.data.length;i++){
        if(this.diff2(this.data[i].time,this.date) > 0 ){
          ret = i - 1
          break
        }
      }
      return ret
    },
    flag(){
      return this.$store.state.flag
    },
    date(){
      return this.$store.state.date
    },
    data_raw(){
      if(this.flag == 'half'){
        return this.data1
      }
      else{
        return this.data2
      }
    },
    data(){
      let ret = []
      if (this.data_raw.length == 0){
        return []
      }
      let day1 = this.data_raw[0]['time']
      for(let i = 0;i<this.data_raw.length;i++){
        let tmp_obj = Object()
        let tmp = this.data_raw[i]
        tmp_obj['time'] = tmp['time']
        tmp_obj['index'] = this.diff(tmp['time'],day1)
        tmp_obj['rate'] = parseFloat(tmp['rate'])
        tmp_obj['rate2'] = parseFloat(tmp['rate2'])
        ret.push(tmp_obj)
        
      }
      return ret
    },
    time_span(){
      let len = this.data_raw.length
      let date1 = this.data_raw[0].time
      let date2 = this.data_raw[len - 1].time
      return this.diff(date1,date2)
    },
    line2(){
      return d3.svg.line()
        .x(function(d){return this.xS(d['index'])})
        .y(function(d){return this.yS(d['rate2'])});
    },
    line(){
      return d3.svg.line()
        .x(function(d){return this.xS(d['index'])})
        .y(function(d){return this.yS(d['rate'])});
    },
    xS(){
      return d3.scale.linear().domain([0,this.time_span,]).range([this.margin,this.width-this.margin]);
    },
    yS(){
      return d3.scale.linear().domain([10,0]).range([this.margin,this.height-this.margin])
    },
    div_css(){
      //div的css属性从这里写
      let div_height = this.height
      let div_width = this.width
      let div_border = '0px solid black'
      let div_position = 'absolute'
      let div_top = this.top //所有边距固定为10
      let div_left = this.left

      return  "position:" + div_position + ";" +
              "height:" + div_height + "px;" +
              "width:" + div_width + "px;" +
              "border:" + div_border + ";" +
              "left:" + div_left + "px;" +
              "top:" + div_top + "px;" 
              


    },
  },
  methods:{
    log(){
      console.log(this.data)
      console.log(this.time_span)
      console.log(this.instant_index)
    },
    diff(day1,day2){
      let date1 = new Date(day1)
      let date2 = new Date(day2)
      return Math.ceil(Math.abs(date1 - date2) / 86400000);
    },
    diff2(day1,day2){
      let date1 = new Date(day1)
      let date2 = new Date(day2)
      return Math.ceil((date1 - date2) / 86400000);
    }
  }
  
}
</script>

